body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

img {
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: transform 0.3s;
}

img:hover {
  transform: scale(1.5);
}

.app-container {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

.sidebar-container {
  grid-row: 1/2;
  grid-column: 1/2;
  background: grey;
  color: white;
}

.sidebar-container h1 {
  text-align: center;
  margin-top: 18px;
}

.sidebar-container p {
  padding: 0 16px;
}

.sidebar-container hr {
  width: 85%;
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(3, 25vh);
}

.image-container {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  border: 1px solid white;
  overflow: hidden;
}

.modal-container {
  width: fit-content;
}

.form-container {
  margin-top: 18px;
}